import { format, formatDistanceToNow } from 'date-fns';
import { ru } from 'date-fns/locale';

export function formatRelativeDate(dateStr) {
  const date = new Date(dateStr);
  const now = new Date();
  const currentYear = now.getFullYear();
  const reviewYear = date.getFullYear();

  // Если меньше 30 дней - показываем относительное время
  if (now - date < 30 * 24 * 60 * 60 * 1000) {
    return formatDistanceToNow(date, { addSuffix: true, locale: ru });
  } 
  // Если тот же год - не показываем год
  else if (reviewYear === currentYear) {
    return format(date, 'd MMMM', { locale: ru });
  } 
  // Если другой год - показываем с годом
  else {
    return format(date, 'd MMMM yyyy', { locale: ru });
  }
}
