/**
 * Утилиты для работы с адаптивными изображениями
 */

/**
 * Определяет оптимальный размер изображения Яндекса на основе контейнера
 * @param {string} originalUrl - Оригинальный URL изображения
 * @param {string} containerSize - Размер контейнера ('thumbnail', 'gallery', 'lightbox')
 * @returns {string} URL с подходящим размером
 */
export function getOptimalImageSize(originalUrl, containerSize = 'thumbnail') {
  if (!originalUrl || !originalUrl.includes('avatars.mds.yandex.net')) {
    return originalUrl;
  }

  // Размеры для разных контейнеров
  const sizeMap = {
    thumbnail: 'M',    // ~300px для миниатюр
    gallery: 'L',      // ~600px для галереи
    lightbox: 'XXL'    // ~1200px для лайтбокса
  };

  const targetSize = sizeMap[containerSize] || 'M';
  
  // Заменяем размер в URL (последний сегмент после последнего слеша)
  return originalUrl.replace(/\/[SMLX]+$/, `/${targetSize}`);
}

/**
 * Создает srcset для адаптивных изображений Яндекса
 * @param {string} originalUrl - Оригинальный URL изображения
 * @returns {string} srcset строка
 */
export function createYandexSrcSet(originalUrl) {
  if (!originalUrl || !originalUrl.includes('avatars.mds.yandex.net')) {
    return '';
  }

  const sizes = [
    { suffix: 'S', width: '150w' },
    { suffix: 'M', width: '300w' },
    { suffix: 'L', width: '600w' },
    { suffix: 'XL', width: '900w' },
    { suffix: 'XXL', width: '1200w' }
  ];

  return sizes
    .map(({ suffix, width }) => {
      const url = originalUrl.replace(/\/[SMLX]+$/, `/${suffix}`);
      return `${url} ${width}`;
    })
    .join(', ');
}

/**
 * Определяет sizes атрибут для адаптивных изображений
 * @param {string} containerType - Тип контейнера
 * @returns {string} sizes строка
 */
export function getImageSizes(containerType = 'thumbnail') {
  const sizesMap = {
    thumbnail: '(max-width: 640px) 150px, (max-width: 768px) 200px, 250px',
    gallery: '(max-width: 640px) 45vw, (max-width: 768px) 30vw, 200px',
    lightbox: '100vw'
  };

  return sizesMap[containerType] || sizesMap.thumbnail;
}
