import { useState, useEffect, useMemo, useCallback } from 'react';
import { getReviewsWithStats } from '../api';

/**
 * Оптимизированная функция прокрутки к началу списка отзывов
 * Использует requestAnimationFrame для лучшей производительности
 */
function scrollToReviewsList() {
  // Используем requestAnimationFrame для избежания блокировки UI
  requestAnimationFrame(() => {
    // Пробуем найти различные элементы в порядке приоритета
    const selectors = [
      '.reviews-list',           // Основной список отзывов
      '.snx-reviews-widget',     // Весь виджет
      '[data-snx-reviews-widget]', // Альтернативный селектор
      '.widget-summary'          // Заголовок виджета
    ];
    
    let target = null;
    for (const selector of selectors) {
      target = document.querySelector(selector);
      if (target) break;
    }
    
    if (target) {
      // Используем более эффективный способ получения позиции
      const targetRect = target.getBoundingClientRect();
      const offsetTop = window.pageYOffset + targetRect.top - 20;
      
      // Проверяем, нужно ли вообще скроллить
      if (Math.abs(window.pageYOffset - offsetTop) > 10) {
        window.scrollTo({ 
          top: offsetTop, 
          behavior: 'smooth' 
        });
      }
    } else {
      // Если ничего не найдено, прокручиваем к началу страницы
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  });
}

/**
 * Хук для получения отзывов организации по CMS ID
 * @param {string} cmsId - CMS ID организации
 * @param {Object} options - Параметры запроса API
 */
export function useReviews(cmsId, options = {}) {
  // Основные данные
  const [reviews, setReviews] = useState([]);
  const [pagination, setPagination] = useState(null);
  const [organization, setOrganization] = useState(null);
  const [stats, setStats] = useState(null);
  
  // Состояния загрузки
  const [isLoading, setIsLoading] = useState(true);
  const [isError, setIsError] = useState(false);
  const [error, setError] = useState(null);
  const [isTransitioning, setIsTransitioning] = useState(false);
  
  // Текущие фильтры
  const [currentFilters, setCurrentFilters] = useState({
    sortBy: 'submitted_at',
    sortOrder: 'DESC',
    hasMedia: undefined
  });
  const [currentPage, setCurrentPage] = useState(1);

  /**
   * Загрузить отзывы с сервера с текущими фильтрами
   */
  const loadReviews = useCallback(async (filters = currentFilters, page = 1, isInitialLoad = false) => {
    if (!cmsId) return;

    if (isInitialLoad) {
      setIsLoading(true);
    } else {
      setIsTransitioning(true);
    }
    
    setIsError(false);
    setError(null);

    try {
      const requestOptions = {
        ...options,
        ...filters,
        limit: options.limit || 10,
        offset: (page - 1) * (options.limit || 10)
      };

      const data = await getReviewsWithStats(cmsId, requestOptions);

      setReviews(data.reviews);
      setPagination(data.pagination);
      
      // Обновляем организацию и статистику только при первой загрузке
      if (isInitialLoad || !organization) {
        setOrganization(data.organization);
        setStats(data.stats);
      }
      
      if (isInitialLoad) {
        setIsLoading(false);
      } else {
        setIsTransitioning(false);
      }

    } catch (err) {
      console.error('[useReviews] Error loading reviews:', err);
      setIsError(true);
      setError(err.message);
      setIsLoading(false);
      setIsTransitioning(false);
    }
  }, [cmsId, options, organization]);

  /**
   * Применить новые фильтры
   */
  const applyFilters = useCallback(async (newFilters) => {
    const updatedFilters = { ...currentFilters, ...newFilters };
    setCurrentFilters(updatedFilters);
    setCurrentPage(1);
    
    // Загружаем отзывы с новыми фильтрами
    await loadReviews(updatedFilters, 1, false);
    
  }, [currentFilters, loadReviews]);

  /**
   * Перейти на страницу
   */
  const goToPage = useCallback(async (page) => {
    if (page === currentPage) return;
    
    scrollToReviewsList();
    setCurrentPage(page);
    
    // Загружаем отзывы для новой страницы
    await loadReviews(currentFilters, page, false);
  }, [currentPage, currentFilters, loadReviews]);

  // Эффект для загрузки данных при монтировании
  useEffect(() => {
    if (cmsId) {
      loadReviews(currentFilters, 1, true);
    }
  }, [cmsId]); // Только при изменении cmsId

  // Эффект для перезагрузки при изменении ключевых опций
  useEffect(() => {
    if (cmsId) {
      setCurrentPage(1);
      loadReviews(currentFilters, 1, false);
    }
  }, [options.limit, options.media, options.replies, options.minRating, options.maxRating, options.hasReply]); // При изменении ключевых опций

  // Вычисляем статистику медиа из stats (если доступна)
  const mediaStats = useMemo(() => {
    // Пытаемся получить общую статистику из API
    if (stats && stats.totalReviewsWithMedia !== undefined) {
      return {
        totalReviewsWithMedia: stats.totalReviewsWithMedia
      };
    }
    
    // Fallback - считаем из текущих отзывов
    const currentWithMedia = reviews.filter(review => review.hasMedia).length;
    return {
      totalReviewsWithMedia: currentWithMedia
    };
  }, [stats, reviews]);

  return {
    reviews, // Отзывы с сервера
    pagination,
    organization,
    stats: stats ? {
      ...stats,
      ...mediaStats // Добавляем статистику медиа
    } : null,
    isLoading,
    isError,
    error,
    currentPage,
    isTransitioning,
    currentFilters, // Добавляем текущие фильтры
    goToPage,
    applyFilters
  };
}
