/**
 * API для работы с отзывами гостей через Reviews Ecosystem API
 * Поддерживает получение отзывов по cms_id организации с фильтрацией, пагинацией и репутацией
 * 
 * Основные возможности:
 * - Получение отзывов с богатыми данными о репутации организации
 * - Автоматический расчет рейтингов и статистики на стороне API
 * - Поддержка медиа-контента и ответов организации
 * - Фильтрация по рейтингу, языку, наличию ответов
 */

// Определяем базовый URL для API
function getApiBaseUrl() {
  const envUrl = import.meta.env.VITE_REVIEWS_API_URL;
  
  // Если URL задан в переменной окружения
  if (envUrl) {
    // Если URL не содержит протокол, добавляем http://
    if (envUrl && !envUrl.startsWith('http://') && !envUrl.startsWith('https://')) {
      return `http://${envUrl}`;
    }
    return envUrl;
  }
  
  // В dev режиме используем прокси Vite (пустая строка)
  // В продакшене - fallback на localhost:3000
  return import.meta.env.DEV ? '' : 'http://localhost:3000';
}

const API_BASE_URL = getApiBaseUrl();


/**
 * Получить отзывы организации по CMS ID
 * @param {string} cmsId - CMS ID организации
 * @param {Object} options - Параметры запроса
 * @param {number} options.limit - Количество отзывов (1-100, по умолчанию 10)
 * @param {number} options.offset - Смещение для пагинации (по умолчанию 0)
 * @param {string} options.sortBy - Поле сортировки ('submitted_at' или 'rating')
 * @param {string} options.sortOrder - Порядок сортировки ('ASC' или 'DESC')
 * @param {boolean} options.hasMedia - Фильтр по наличию медиа
 * @param {number} options.minRating - Минимальный рейтинг (1-5)
 * @param {number} options.maxRating - Максимальный рейтинг (1-5)
 */
export async function getReviewsByCmsId(cmsId, options = {}) {
  const {
    limit = 10,
    offset = 0,
    sortBy = 'submitted_at',
    sortOrder = 'DESC',
    hasMedia,
    minRating,
    maxRating
  } = options;

  // Базовые параметры
  const params = new URLSearchParams({
    limit: Math.min(Math.max(1, limit), 100).toString(),
    offset: Math.max(0, offset).toString(),
    sortBy: ['submitted_at', 'rating'].includes(sortBy) ? sortBy : 'submitted_at',
    sortOrder: ['ASC', 'DESC'].includes(sortOrder) ? sortOrder : 'DESC'
  });

  // Добавляем опциональные параметры фильтрации
  if (hasMedia !== undefined) {
    params.append('hasMedia', hasMedia.toString());
  }
  if (minRating !== undefined && minRating >= 1 && minRating <= 5) {
    params.append('minRating', minRating.toString());
  }
  if (maxRating !== undefined && maxRating >= 1 && maxRating <= 5) {
    params.append('maxRating', maxRating.toString());
  }

  const url = `${API_BASE_URL}/api/reviews/organization/${cmsId}?${params}`;
  
  let response;
  try {
    response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      mode: 'cors'
    });
    
    if (!response.ok) {
      throw new Error(`API Error: ${response.status} ${response.statusText}`);
    }
  } catch (fetchError) {
    if (fetchError.name === 'TypeError' && fetchError.message.includes('Failed to fetch')) {
      throw new Error(`Не удалось подключиться к API сервису. Проверьте, что сервер запущен на ${API_BASE_URL}`);
    }
    throw fetchError;
  }

  const result = await response.json();
  
  if (!result.success) {
    throw new Error(result.error || 'Unknown API error');
  }

  return {
    reviews: result.data.map(mapReviewFromAPI),
    pagination: result.pagination,
    organization: result.organization,
    embedded: result.embedded
  };
}

/**
 * Получить информацию об организации с репутацией
 * @param {string} cmsId - CMS ID организации
 */
export async function getOrganizationInfo(cmsId) {
  // Получаем информацию об организации из API отзывов
  const { organization } = await getReviewsByCmsId(cmsId, { limit: 1 });
  
  if (organization) {
    return {
      id: organization.organization_id,
      name: organization.name,
      slug: organization.slug,
      cmsId: organization.cms_id,
      reputation: organization.reputation
    };
  }
  
  return null;
}

/**
 * Получить детальную репутацию организации
 * @param {string} cmsId - CMS ID организации
 */
export async function getOrganizationReputation(cmsId) {
  const { organization } = await getReviewsByCmsId(cmsId, { limit: 1 });
  
  if (!organization?.reputation) {
    return null;
  }
  
  return {
    ...organization.reputation,
    organization: {
      id: organization.organization_id,
      name: organization.name,
      slug: organization.slug,
      cmsId: organization.cms_id
    }
  };
}

/**
 * Получить отзывы с репутацией организации за один запрос
 * @param {string} cmsId - CMS ID организации
 * @param {Object} options - Параметры запроса
 */
export async function getReviewsWithStats(cmsId, options = {}) {
  const response = await getReviewsByCmsId(cmsId, options);
  
  if (!response.organization?.reputation) {
    return {
      ...response,
      stats: null
    };
  }
  
  // Используем данные репутации из API вместо ручного расчета
  const reputation = response.organization.reputation;
  
  // Дополнительная статистика по текущей выборке отзывов
  const reviewsWithReplies = response.reviews.filter(review => review.hasReply).length;
  const reviewsWithMedia = response.reviews.filter(review => review.hasMedia).length;
  
  // Получаем общее количество отзывов с медиа из API (делаем отдельный запрос)
  let totalReviewsWithMedia = reviewsWithMedia; // fallback к текущей выборке
  try {
    const mediaStatsResponse = await getReviewsByCmsId(cmsId, { 
      limit: 1, 
      hasMedia: true 
    });
    totalReviewsWithMedia = mediaStatsResponse.pagination?.total || reviewsWithMedia;
  } catch {
    // Тихо игнорируем ошибку получения статистики медиа
  }
  
  const stats = {
    // Основная статистика из репутации API
    totalReviews: reputation.total_reviews,
    avgRating: reputation.average_rating,
    weightedRating: reputation.weighted_rating,
    ratingDistribution: reputation.rating_distribution,
    sourcesBreakdown: reputation.sources_breakdown,
    lastCalculated: reputation.last_calculated,
    algorithm: reputation.algorithm,
    sourcesCount: reputation.sources_count,
    
    // Дополнительная статистика по текущей выборке
    reviewsWithReplies,
    reviewsWithMedia,
    
    // Общая статистика медиа
    totalReviewsWithMedia,
    
    // Информация об организации
    organization: {
      id: response.organization.organization_id,
      name: response.organization.name,
      slug: response.organization.slug,
      cmsId: response.organization.cms_id
    }
  };
  
  return {
    ...response,
    stats
  };
}

/**
 * Получить статистику отзывов организации (deprecated - используйте getReviewsWithStats)
 * @param {string} cmsId - CMS ID организации
 */
export async function getOrganizationStats(cmsId) {
  // Используем новую функцию для получения статистики
  const { stats } = await getReviewsWithStats(cmsId, { limit: 1 });
  
  if (!stats) {
    return null;
  }

  // Возвращаем данные в старом формате для обратной совместимости
  return {
    totalReviews: stats.totalReviews,
    avgRating: stats.avgRating,
    reviewsWithReplies: stats.reviewsWithReplies,
    reviewsWithMedia: stats.reviewsWithMedia,
    organization: stats.organization
  };
}

/**
 * Маппинг данных отзыва из API формата в формат виджета
 */
function mapReviewFromAPI(apiReview) {
  return {
    id: apiReview.review_id,
    sourceReviewId: apiReview.source_review_id,
    content: apiReview.content,
    rating: apiReview.rating,
    date: apiReview.submitted_at,
    language: apiReview.language,
    source: apiReview.source,
    
    // Информация о госте
    guest: {
      id: apiReview.guest?.guest_id,
      name: apiReview.guest?.full_name,
      firstName: apiReview.guest?.first_name,
      lastName: apiReview.guest?.last_name,
      avatar: apiReview.guest?.avatar_url,
      gender: apiReview.guest?.gender,
      loyaltyLevel: apiReview.guest?.loyalty_level
    },
    
    // Медиа контент - строгая проверка на наличие реальных медиа-файлов
    hasMedia: Boolean(
      apiReview.media_assets && 
      Array.isArray(apiReview.media_assets) && 
      apiReview.media_assets.length > 0 &&
      apiReview.media_assets.some(asset => asset && asset.source_url)
    ),
    mediaCount: (apiReview.media_assets && Array.isArray(apiReview.media_assets)) 
      ? apiReview.media_assets.filter(asset => asset && asset.source_url).length 
      : 0,
    mediaAssets: (apiReview.media_assets && Array.isArray(apiReview.media_assets)) 
      ? apiReview.media_assets.filter(asset => asset && asset.source_url)
      : [],
    
    // Ответы организации
    hasReply: apiReview.organization_replies && apiReview.organization_replies.length > 0,
    organizationReplies: apiReview.organization_replies || [],
    // Добавляем первый ответ организации для совместимости с компонентом
    reply: apiReview.organization_replies && apiReview.organization_replies.length > 0 
      ? {
          text: apiReview.organization_replies[0].reply_text,
          author: apiReview.organization_replies[0].author_name || 'Ответ организации',
          date: apiReview.organization_replies[0].replied_at,
          isOfficial: apiReview.organization_replies[0].is_official || false
        }
      : null,
    
    // Метаданные
    isBlocked: apiReview.is_blocked,
    currentStatus: apiReview.current_status,
    orderId: apiReview.order_id,
    bookingId: apiReview.booking_id,
    organizationId: apiReview.organization_id,
    guestId: apiReview.guest_id,
    sourceId: apiReview.source_id
  };
}

// Экспорт для обратной совместимости (deprecated)
export async function getReviews() {
  return [];
}
